<template>
  <div>
    <div class="flex justify-between items-center mb-8">
      <div class="lg:flex lg:divide-x lg:divide-grey-01">
        <div class="flex items-center lg:pr-4 lg:mb-0 mb-4">
          <router-link :to="{ name: 'integrationUspsTab' }" class="text-dark mr-4"><span class="material-icons">chevron_left</span></router-link>
          <h6>Add USPS Account</h6>
        </div>
      </div>
    </div>
    <Fieldset class="rounded-md mb-6">
      <template #label>Account Number</template>
      <IntegrationUspsForm :data="data" ref="refForm" />
    </Fieldset>
    <div class="flex justify-end">
<!--      <Button view="outlined" color="teal" :to="{ name: 'integrationUspsTab' }" link>Back</Button>-->
      <Button color="teal" :loading="loading" @click="createWorkflow">Save</Button>
    </div>
  </div>
</template>

<script>
import { defineComponent, ref } from 'vue'
import { useRouter } from 'vue-router'
import { useShowUspsForm } from 'composable/integration'
import Button from 'bbUi/components/button/button.vue'
import Card from 'bbUi/components/card/card.vue'
import Fieldset from 'bbUi/components/fieldset/fieldset.vue'
import IntegrationUspsForm from 'components/Integration/Usps/Form.vue'
import Breadcrumb from 'bbUi/components/breadcrumb/breadcrumb.vue'

export default defineComponent({
  components: {
    Button,
    Card,
    Fieldset,
    IntegrationUspsForm,
    Breadcrumb
  },
  setup() {
    const router = useRouter()
    const refForm = ref(null)
    const { data, loading, actionCreate } = useShowUspsForm()
    const createWorkflow = async () => {
      if(!refForm.value.validate || (refForm.value.validate && refForm.value.validate())){
        await actionCreate()
        await router.push({
          name: 'integrationUspsTab'
        })
      }
    }

    return {
      data,
      loading,
      createWorkflow,
      refForm
    }
  }
})
</script>